.cat {
  width: 400px;
  height: 200px;
  margin: 50px auto 0;
}

.move {
  -webkit-animation: move-animation 20s infinite;
          animation: move-animation 20s infinite;
}

.walking {
  -webkit-animation: walk-animation 2s steps(12) infinite;
          animation: walk-animation 2s steps(12) infinite;
  background: url(/static/media/cat_tiles.da843e98.png) 0 0 no-repeat;
}

.sitting {
  -webkit-animation: sit-animation 2s steps(6) infinite;
          animation: sit-animation 2s steps(6) infinite;
  background: url(/static/media/cat_tiles.da843e98.png) 0 0 no-repeat;
}

.walk-then-sit {
  -webkit-animation: walk-animation 2s steps(12) 1, sit-animation 3s 2s steps(6) 1;
          animation: walk-animation 2s steps(12) 1, sit-animation 3s 2s steps(6) 1;
  background: url(/static/media/cat_tiles.da843e98.png) 0 0 no-repeat;
}

.jumping {
  -webkit-animation: jump-animation 15s steps(12) infinite;
          animation: jump-animation 15s steps(12) infinite;
  background: url(/static/media/cat_tiles.da843e98.png) 0 0 no-repeat;
}

@-webkit-keyframes walk-animation {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 0 -2391px;
  }
}

@keyframes walk-animation {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 0 -2391px;
  }
}

@-webkit-keyframes move-animation {
  from {
    margin-left: 120%;
  }

  to {
    margin-left: -120%;
  }
}

@keyframes move-animation {
  from {
    margin-left: 120%;
  }

  to {
    margin-left: -120%;
  }
}

@-webkit-keyframes sit-animation {
  from {
    background-position: -400px 0;
  }

  to {
    background-position: -400px -1200px;
  }
}

@keyframes sit-animation {
  from {
    background-position: -400px 0;
  }

  to {
    background-position: -400px -1200px;
  }
}

@-webkit-keyframes jump-animation {
  from {
    background-position: -800px 0;
  }

  to {
    background-position: -800px -1200px;
  }
}

@keyframes jump-animation {
  from {
    background-position: -800px 0;
  }

  to {
    background-position: -800px -1200px;
  }
}

